@charset "UTF-8";
/* Mixins */
.actions-bottom {
  position: fixed;
  bottom: 0;
  left: 50%;
  display: flex;
  align-items: center;
  padding-bottom: 50px;
  transform: translateX(-50%); }

.actions-right {
  position: fixed;
  display: flex;
  top: 0;
  right: 0;
  padding: 45px 15px 0 0; }

.actions-slider {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 50px;
  height: 125px;
  margin: 0 15px; }
  .actions-slider > input[type=range] {
    margin-top: 90px;
    -webkit-appearance: none;
    width: 120px;
    transform: rotate(270deg);
    background: transparent; }
    .actions-slider > input[type=range]:focus {
      outline: none; }
    .actions-slider > input[type=range]::-webkit-slider-thumb {
      -webkit-appearance: none;
      margin-right: -5px;
      background-color: #009be3;
      border: 3px solid white;
      width: 15px;
      height: 15px;
      border-radius: 50%;
      box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
      transform: translateY(-4px);
      cursor: pointer; }
    .actions-slider > input[type=range]::-webkit-slider-runnable-track {
      width: 100%;
      height: 7px;
      cursor: pointer;
      background: white;
      border-radius: 10px; }
  .actions-slider .sample {
    border-radius: 50%;
    border: 1px solid rgba(255, 255, 255, 0.7); }
    .actions-slider .sample.color {
      width: 30px;
      height: 30px;
      border: 3px solid white; }
    .actions-slider .sample-wrapper {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 50px;
      height: 50px; }

.actions-btn {
  width: 50px;
  height: 50px;
  background-color: white;
  border-radius: 50%;
  box-shadow: 0 0 40px rgba(0, 0, 0, 0.1); }
  .actions-btn.big {
    width: 70px;
    height: 70px;
    margin: 0 20px; }
  .actions-btn > .icon-valid {
    width: 50px;
    height: 21px;
    color: #22d622; }
  .actions-btn > .icon-rotate {
    width: 15px;
    height: 21px;
    color: #009be3; }
  .actions-btn > .icon-clear {
    width: 15px;
    height: 21px;
    color: red; }

.brush {
  position: fixed;
  top: 0;
  left: 0;
  opacity: 0;
  border-radius: 50%;
  border-style: solid;
  border-width: 1px;
  transition: opacity 0.1s ease-in-out;
  pointer-events: none; }
  .brush.on {
    opacity: 1; }

.ticket {
  position: absolute;
  top: 0;
  height: 100%;
  left: 0;
  width: 100%;
  pointer-events: none; }
  .ticket-inner {
    height: 100%;
    width: 100%;
    padding: 20px;
    background-color: aliceblue;
    box-shadow: 0 0 40px rgba(0, 0, 0, 0.1);
    pointer-events: none; }
  .ticket canvas {
    pointer-events: all; }
  .ticket .renderer {
    background-color: white; }
  .ticket-wrapper {
    position: relative;
    width: 100%;
    max-width: 580px;
    height: 900px;
    margin: 0 auto;
    transform-origin: 50% 50%; }
    .ticket-wrapper.rotated {
      transform: rotate(90deg); }

.background {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #009be3;
  z-index: -1; }

.App {
  padding: 80px 0; }

